    import app from '@/common/app.js';
    import request from '@/common/request.js';
    import util from '@/common/util.js';
	export default {
		data() {
			return {
				
			}
		},
		methods: {
			call(){
                wx.requestSubscribeMessage({
                  tmplIds: [app.TEMPLATE_IDS.JFTZ],
                  success (res) {
                      if(res[app.TEMPLATE_IDS.JFTZ]=="accept"){
                          request.http({
                              url: "/app/demo/jftz",
                              header: 'application/x-www-form-urlencoded',
                              success: (result) => {
                                  uni.showToast({title: "通知已经发送，请查阅微信"});
                                  console.log("ss"+result);
                              },
                              error:(result)=>{
                                  debugger
                                  console.log("ee"+result);
                              }
                           });
                      }else{
                          uni.showToast({title: "通知用户拒绝订阅"});
                      }
                  },
                  error(res){
                      console.log("er-->"+res);
                  },
                  complete(res){
                      console.log("co-->"+res);
                  }
                })
            }
		}
	}
